}
input:focus:-moz-placeholder {
  opacity: 0;
}
input:focus:-ms-input-placeholder {
  opacity: 0;
}
textarea:focus::-webkit-input-placeholder {
  opacity: 0;
}
textarea:focus::-moz-placeholder {
  opacity: 0;
}
textarea:focus:-moz-placeholder {
  opacity: 0;
}
textarea:focus:-ms-input-placeholder {
  opacity: 0;
}




input:not([type="checkbox"]):not([type="radio"]), textarea {
  display: block;
  width: 100%;
}
textarea {
  resize: none;
}.send-form, .webform-client-form {
  position: relative;
  font-size: 20px;
}.form-group {
  margin-bottom: 2em;
  padding: 0;
  border: 0;&:after {
    content: '';
    display: table;
    clear: both;
  }
}.form-half {
  float: left;
  width: 50%;&:nth-child(odd) {
    padding-right: 3em;
  }&:nth-child(even) {
    padding-left: 3em;
  }
}.dates {
  > .title {
    margin-bottom: 2em;
  }
}.form-inline-group {
  display: flex;.send-form__item {
    width: 120px;
    margin-right: 2em;&:last-child {
      margin: 0;
    }
  }
}
.form-item {
  position: relative;
  &.active label {
    transform: translateY(-1.6em) scale(0.85);
    transform-origin: 0 0;
  }
  &.select {
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 1em;
      width: 1em;
      height: 1em;
      border-bottom: 1px solid $cc;
      border-left: 1px solid $cc;
      transform: rotate(-45deg);
    }&.open:before {
      transform: rotate(135deg);
    }
  }&.error {
    &:after {
      opacity: 1;
    }
    label, .in-text,input, textarea {
      border-color: red;
      // span {
      //   opacity: 1;
      // }
    }
  }

  input, select {
    display: block;
    width: 100%;
    padding: 15px 0;
    // height: 3.2em;
    margin-bottom: 24px;
    background: #fff;
    border: none;
    outline: none;
    border-bottom: 1px solid #808080;
    @media (max-width: 1300px) {
      margin-bottom: 14px;
      padding: 10px 0;
    }
   @media (max-width: 767px) {
      font-size: 16px;
      margin-bottom: 10px;
      padding: 7px 0;
    }
  }
  select {
    text-transform: uppercase;
    -ms-appearance: none;
    appearance: none;
  }

  textarea {
    padding: 15px 0;
    display: block;
    width: 100%;
    overflow: hidden;
    max-height: 53px;
    background: #fff;
    border: none;
    outline: none;
    border-bottom: 1px solid #808080;
    @media (max-width: 1300px) {
      padding: 10px 0;
      max-height: 43px;
    }
    @media (max-width: 767px) {
      font-size: 16px;
      max-height: 100%;
    }
  }

  label {
    position: absolute;
    top: 1.4em;
    left: 0;
    font-family: $ff;
    transition: transform 0.2s ease-out;
    transform-origin: 0 100%;
    pointer-events: none;
    span {
      display: inline-block;
      opacity: 0;
      transition: opacity 0.5s;
    }
  }
  &.checkox {
    position: relative;
    margin-top: 20px;
    input {
      position: absolute;
      left: 0;
      top: 0;
      display: inline-block;
      width: 22px;
      height: 22px;
      opacity: 0;
      margin-bottom: 0;
      &:checked + label:after {
        opacity: 1;
      }
    }
    label {
      padding-left: 30px;
      pointer-events: auto;
      position: relative;
      display: inline-block;
      cursor: pointer;
      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
      }
      &:before{
        width: 22px;
        height: 22px;
        border: 2px solid #0000FF;
        -webkit-border-radius: 1px;
        left: 0;
        top: 3px;
        }
      &:after {
        width: 8px;
        height: 12px;
        border-right: 2px solid #0000FF;
        border-bottom: 2px solid #0000FF;
        transform: rotate(40deg);
        opacity: 0;
        left: 7px;
        top: 6px;
      }
      span {
        font-size: 14px;
        opacity: 1;
        font-family: $ff-text;
        font-weight: 500;
      }
    }

  }
  input, textarea, select {
    &:focus + label {
      transform: translateY(-1.6em) scale(0.85);
      transform-origin: 0 0;
    }
  }.in-text {
    position: relative;
    height: 3.2em;
    line-height: 3.2em;
    background: #fff;
    border-bottom: 1px solid #341209;& after {
      content: "\e913";
      font-family: 'font-icon' !important;
      position: absolute;
      font-size: 33px;
      bottom: 0px;
      right: 0;
      line-height: 1;
      transform: translateY(-50%);
      pointer-events: none;
    }

    span {
      font-weight: 500;
      text-transform: uppercase;
    }
    input {
      position: absolute;
      opacity: 0;
      top: 0;
      left: 0;
    }
  }
}
.form-check {
  input {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;&:checked + label:after {
      visibility: visible;
    }
  }
  label {
    position: relative;
    display: flex;
    align-items: center;&:before {
      content: '';
      flex-shrink: 0;
      display: inline-block;
      margin-right: 8px;
      width: 24px;
      height: 24px;
      border: 1px solid #f93;
    }&:after {
      content: '';
      position: absolute;
      top: 3px;
      left: 9px;
      width: 8px;
      height: 15px;
      border: 2px solid #f93;
      border-top: 0;
      border-left: 0;
      transform: rotate(35deg);
      visibility: hidden;
    }
  }&.error {
    label {
      color: red;
    }
  }
}.form-action {
  margin-top: 50px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &.disabled {
    [type='submit'], input[type='submit'] {
      opacity: 0.3;
      pointer-events: none;
    }
  }
  @media (max-width: 1280px) {
      margin-top: 20px;
  }
  @media (max-width: 480px) {
      margin-top: 10px;
  }
  [type='submit'], input[type='submit'] {
    border: 0;
    outline: none;
    background-color: #FFF;
    display: inline-block;
    font-family: $ff;
    font-weight: 800;
    font-size: 16px;
    color: #00F;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 768px) {
      font-size: 14px;
    }
    &:hover {
      color: #808080;
      svg {
        fill: #808080;
        transform: translateX(3px);
      }
    }
  }
  svg {
    transition: all .3s ease;
    fill: #0000FF;
    max-width: 32px;
    margin-left: 15px;
  }
}
.form-thank {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 10%;
  font-size: 2em;
  background: #fff;
  display: none;.wrap {
    width: 100%;
    height: 100%;
    border: 1px solid #333;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;
  }

  h4 {
    width: 100%;
    text-transform: uppercase;
    font-weight: 300;
  }
}
.send-form__item, .send-form__check {
  &.error {
    background: none;
  }
}

.contact-form {
  padding-top: 130px;
  margin-bottom: 40px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 98;
  background-color: #FFF;
  transform: translateY(200%);
  transition: transform .5s ease;
  @media (max-width: 1300px) {
    padding: 85px 15px 0;
  }
  .container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.show {
   transform: translateY(0%);

  }
  &__title {
    margin-bottom: 60px;
    @media (max-width: 1280px) {
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      svg {
        height: 40px;
      }
    }
  }
  &__info {
    font-size: 14px;
    line-height: 24px;
    font-weight: 500;
    margin-bottom: 40px;
    @media (max-width: 1280px) {
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      line-height: 1.2;
    }
  }
  &__inner {
    max-width: 700px;
    margin: auto;
  }
  &__wrap {
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  &__back {
    margin-bottom: 40px;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 3;
    @media (max-width: 1024px) {
      margin-bottom: 0;
    }
  }
}

/* == media == */
@media screen and (max-width:900px) and (orientation:landscape) {
 .header .container {
   height: 60px;
 }
  .contact-form__title {
    margin-bottom: 10px;
    text-align: center;
  }
  .contact-form__title svg{
    max-width: 300px;
    max-height: 30px;
    margin: 0 auto;
  }
  .contact-form__info {
    font-size: 12px;
    line-height: 1.2;
    margin-bottom: 10px;
  }
  .form-item input, .form-item select, .form-item textarea {
    margin-bottom: 5px;
    font-size: 12px;
    max-height: 30px;
    padding: 5px 0;
  }
  .form-item.checkox {
    margin-top: 10px;
  }
  .form-action {
    margin-top: 5px;
  }
  .form-action [type='submit'], .form-action input[type='submit'] {
    font-size: 14px;
    line-height: 1;
  }
}
