.header {
  z-index: 99;
  top: 0;
  left: 0;
  // margin-bottom: 20px;
  width: 100%;
  background:rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(4px);
  color: #000;
  position: fixed;
  opacity: 0;
  transition: background 0.3s ease 0.2s;
  &:before {
    content:"";
    display: block;
    width: 0%;
    height: 100vh;
    background-color: #00F;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 99;
    transform: translateY(99%);
    transition: transform .7s ease, width 0s ease .7s;
  }
  .link  {
    display: block;
    font-family:$ff;
    font-size:12px;
    font-style:normal;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    transition: color .5s ease;
    text-decoration: none;
    &:hover {
      color: #0000FF;
    }
  }
  .container {
    height:  115px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1280px) {
      height: 85px;
    }
    @media (max-width: 480px) {
      height: 80px;
    }
  }
  .logo {
    width: 190px;
    svg {
      fill: #000;

    }
  }
  &__nav-outer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__menu-btn {
    transform: translateX(-1700%);
    opacity: 0;
    pointer-events: none;
    transition: transform .3s ease, opacity .3s ease .2s;
    cursor: pointer;
    position: relative;
    width: 35px;
    height: 26px;
    margin-left: 20px;
    @media (max-width: 1024px) {
      transform: translateX(0);
      opacity: 1;
      pointer-events: auto;
    }
      &:before, &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 46%;
        width: 100%;
        height: 5px;
        background: #fff;
        transition: 0.3s;
        opacity: 0;
      }
      svg {
        fill:#0000FF;
      }
    }
  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    ul {
      width: 100%;
      display: flex;
      transform: translatex(40px);
      transition: opacity .3s ease 0.2s;
      @media (max-width: 1024px) {
        display: none;
      }
    }
    li {
      display: inline-block;
      margin: 0 15px;
      text-align: left;
    }
  }
}
.menu {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 98;
  padding-top: 30vh;
  justify-content: space-between;
  flex-direction: column;
  transform: translateY(100%);
  transition: transform .5s ease 0.1s;
  @media (max-width: 1280px) {
    padding-top: 20vh;
  }
  @media (max-width: 767px) {
    justify-content: center;
    padding-top: 15vh;
  }
  .footer {
    background-color: #000;
    color: #FFF;
    height: auto;
    padding: 20px 0 60px;
     @media (max-width: 767px) {
      display: none;
    }
  }
  .link {
    font-family: $ff;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 34px;
    font-weight: 800;
    transition: color .5s ease;
    text-decoration: none;
    &:hover {
      color: #0000FF;
    }
     @media (max-width: 767px) {
      font-size: 20px;
     }
  }
  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
     @media (max-width: 1024px) {
      display: block;
      li {
        margin-bottom: 20px;
      }
    }
  }
  .contact-us {
    padding: 20px 0;
     @media (max-width: 767px) {
      .contact-us__title {
        font-size: 18px;
      }
    }
  }
}
.mobile-perfect.header__perfect {
  display: none;
  span {
    font-size: 0.8em;
  }
}
.header__container {
  transition: height 0.3s;
}

.scroll {
  .lang {
    display: none;
  }
  .header .container {
    height: 65px;
  }
  @media (min-width: 1024px) {
    .header__nav ul {
      opacity: 0;
      pointer-events: none;
      transition: opacity .1s ease 0s;
    }
    .header__menu-btn {
      pointer-events: auto;
      transform: translateX(0);
      opacity: 1;
      transition: transform .5s ease, opacity .3s ease 0s;
    }
  }
}
.openNav {
  .menu {
    color: #FFF;
    display: flex;
    transform: translateY(0);
    transition: transform 0s ease 1s;
    li {
      animation: up-anim 3.5s ease;
    }
    .contact-us {
      animation: up-anim 3s ease;
    }
    .menu_footer {
      animation: up-anim 3s ease;
    }
  }
  .header {
    background-color: #000;
    transition: background 0.3s ease 1s;
    &:before {
      width: 100%;
      transform: translateY(-100%);
      transition: transform 1s ease 1s, width .7s linear 0s;
    }
    .logo svg {
      fill: #fff;
      transition: fill 0.3s ease 1.5s;
    }
  }
  .header__nav ul {
    opacity: 0;
    pointer-events: none;
  }
  .header__menu-btn {
      transform: translateX(0) !important;
    svg {
      opacity: 0;
      transition: opacity 0.3s ease 1.5s;
    }
    &:before, &:after  {
      top: 46%;
      opacity: 1;
      transition: opacity 0.3s ease 1.5s;

    }
    &:before {
      transform: rotate(45deg);
    }&:after {
      transform: rotate(-45deg);
    }
  }
  &.cf-open {
    .header {
      background-color :#fff;
      transition: background 0.3s ease 0s;
    }
    .logo svg {
      fill: #000;
      transition: fill 0.3s ease 0s;

    }
    .header__menu-btn {
      &:before, &:after  {
      transition: opacity 0.3s ease 0s;

    }
      &:before {
      background-color: #000;
      }&:after {
      background-color: #000;
      }
    }
  }
}
@media (max-width: 1200px) {
  .header__container {
    height: 65px;
  }.desctop-perfect.header__perfect {
    display: none;
  }.mobile-perfect.header__perfect {
    display: block;
  }.hamburger {
    display: block;
    flex-shrink: 0;
  }
}
