.who-we-are {
	margin-top: 150px;
	padding-bottom: 0;
	@media (max-width: 767px) {
		margin-top: 120px;
	}
	&__main-title {
		letter-spacing: -1px;
		margin-bottom: 0;
		&_bottom {
			margin-top: -25px;
			text-align: right;
			margin-bottom: 10px;
    		@media (max-width: 1280px) {
    			margin-top: 25px;
			}
		}
	}
	.ta-right {
		text-align: right;
	}
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		margin-bottom: 75px;
    	@media (max-width: 767px) {
    		display: block;
		}
	}
	&__col {
		&:first-child {
			width: 44%;
    		@media (max-width: 1280px) {
    			margin-right: 15px;
			}
	    	@media (max-width: 767px) {
	    		width: 100%;
	    	}
		}
		&:last-child {
			width: 66%;
			@media (max-width: 767px) {
	    		width: 100%;
	    	}
		}
	}
	&__img {
		padding-bottom: 71%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		margin-top: 72px;
		@media (max-width: 767px) {
			margin-top: 60px;
		}
	}
	h3 {
		text-transform: capitalize ;
		font-size: 56px;
		line-height: 80px;
		font-weight: 800;
		margin-bottom: 0;
		@media (max-width: 768px) {
    		font-size: 35px;
    		line-height: 1;
		}
	}
	&__top {
		margin-bottom: 0;
		.btn {
			margin-top: 177px;
			@media (max-width: 767px) {
				margin-top: 35px;
			}
		}
	}
	&__middle{
		padding-bottom: 40px;
		p {
			max-width: 470px;
		}
	}
	&__bottom{
		padding-top: 30px;
		border-top: 4px solid #0000ff;
		h3 {
			margin-bottom: 47px;
			&:last-child {
				text-align: right;
			}
		}
		.about__col {
			width: 43%;
		}
		li {
			margin-bottom: 40px;
			@media (max-width: 767px) {
				margin-bottom: 20px;
			}
		}
		.about__row {
			margin-bottom: 0;
		}
	}
	.title {
		&_bottom {
			text-align: right;
			margin-bottom: 10px;
			@media (max-width: 1280px) {
				margin-top: 10px;
				margin-bottom: 30px;
			}
		}
	}
}
.employees {
	.container {
		width: 79%;
    	@media (max-width: 1024px) {
    		width: 100%;
		}
	}
	&__row {
		display: flex;
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		margin: 0 -10px 55px;
		@media (max-width: 1024px) {
			margin: 0 0 35px;
		}
		@media (max-width: 600px) {
			margin: 0;
		}
		&:last-child {
			margin: 0 -10px;
		}
		&:nth-child(2n) {
			margin: 0 -105px 55px 90px;
    		@media (max-width: 1024px) {
				margin: 0 0 35px;
			}
			@media (max-width: 600px) {
				margin: 0;
			}
		}
	}
	&__item-outer {
		width: 25%;
		padding: 0 15px;
		text-align: center;
		@media (max-width: 600px) {
			width: 50%;
			margin-bottom: 20px;
			padding: 0;
		}
		@media (max-width: 420px) {
			width: 100%;
		}
	}
	&__item {
		max-width: 170px;
		margin: auto;
		img {
			margin-bottom: 15px;
		}
	}
	&__name {
		text-align: left;
	}
}

.num-info {
	margin-top: 100px;
	margin-bottom: 150px;
	padding: 0 100px;
	display: flex;
	justify-content: space-between;
	@media (max-width: 767px) {
		margin: 30px 0 50px;
		padding: 0;
	}
	@media (max-width: 480px) {
		display: block;
		text-align: center;
	}
	&__num {
		color: #0000ff;
		font-family: $ff;
		font-size: 56px;
		line-height: 80px;
		margin-bottom: 20px;
		@media (max-width: 767px) {
			font-size: 30px;
			line-height: 1;
		}
		@media (max-width: 480px) {
			text-align: center;
		}
	}
	&__item {
		text-align: left;
		padding: 0 5px;
	}
	&__desc {
		text-transform: uppercase;
		@media (max-width: 480px) {
			text-align: center;
		}
	}
}
@media (min-width: 1025px) {

	.team-page.show {
		.who-we-are__main-title {
	   			animation: up-anim 2s ease ;

			&_bottom {
	    		animation: up-anim 2s ease 0.2s;

			}
		}
		.who-we-are__col:first-child {
	    	animation: up-anim 2s ease 0.3s;

		}
		.who-we-are__img {
	    	animation: up-anim 2s ease 0.3s;

		}
	}
}
