.project{
	padding-top: 155px;
	@media (max-width: 767px) {
		padding-top: 120px;
	}
	img {
		margin-bottom: 40px;
	}
	&__hero{
		margin-bottom: 80px;
		@media (max-width: 1240px) {
			margin-bottom: 40px;
		}

	}
	&__hero-inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 1240px) {
			flex-wrap: wrap;
		}
	}
	&__hero-img {
		max-width: 824px;
		margin-bottom: 20px;
		img {
			margin-bottom: 0;
		}
		@media (max-width: 1240px) {
			order: 2;
			max-width: 100%;
		}
	}
	&__img-lg {
		img {
			width: 100%;
			height: auto;
		}
	}
	&__text-img {
		display: flex;
		justify-content: space-between;
		align-items: center;
		p {
			padding-right: 20px;
		}
		@media (max-width: 1240px) {
			display: block;
			p {
				padding-right: 0;
				margin-bottom: 40px;
			}
		}
	}
	&__hero-info {
		color: #999999;
		position: relative;
		span {
			&:first-child{
		      padding-right: 21px;
		      position: relative;
		      &:after {
		        content: "";
		        display: block;
		        height: 20px;
		        width: 1px;
		        background-color: #999;
		        position: absolute;
		        right: 0;
		        top: 50%;
		        transform: translateY(-50%);
		      }
		    }
		    &:last-child{
		      padding-left: 20px;
		      &:after {
		        content: none;
		      }
		    }
		}
	}
	&__img-img {
		display: flex;
		justify-content: space-between;
		align-items: center;
		img {
			&:first-child {
				width: 55%;
			}
			&:last-child {
				width: 42%;
			}
		}
	}
	&__hero-label {
		font-size: 16px;
		padding-left: 67px;
		color: #000;
		font-family: $ff;
		display: flex;
		justify-content: flex-end;
		align-items: flex-end;
		span:first-child {
			&:after {
				background-color: #000;
				top: auto;
				bottom: 0;
				transform: none;
			}
		}
		@media (max-width: 1240px) {
			order: 1;
			width: 100%;
			padding-left: 0;
			margin-bottom: 40px;
			display: block;
			text-align: left;
		}
	}
	&__back {
		color: #000;
		margin-bottom: 40px;
		svg {
			fill: #000;
		}
	}
	&__main {
		margin-bottom: 80px;
	}
	&__text {
		max-width: 824px;
		margin-bottom: 80px;
		@media (max-width: 1240px) {
			margin-bottom: 40px;
		}
	}
	&__title {
		position: relative;
		h3 {
			max-width: 824px;
		}
	}
	&__date {
		position: absolute;
		top: 0;
		right: 0;
		text-transform: uppercase;
		font-family: $ff;
		font-size: 16px;
		span {
			&:first-child {
				margin-right: 5px;
			}
		}
		@media (max-width: 1240px) {
			position: static;
			margin-bottom: 20px;
		}
	}
	.next{
		padding: 110px 0;
		background-size: cover;
		background-repeat: no-repeat;
		&__link {
			text-decoration: none;
			font-size: 14px;
			margin-bottom: 40px;
			display: inline-block;
    		transition: color .3s ease;
    		color: #FFF;
			&:hover {
				color: $hover;
			}
		}
		&__title {
			max-width: 628px;
			color: #FFF;
			a{
				text-decoration: none;
			}
		}
	}
}
