
@function font-url($file) {
  @return '../fonts/' + $file
}

@mixin webfont($family, $file, $weight: 'normal', $hack-chrome-windows: false) {
@font-face {
    font-family: $family;
    src: url(font-url($file + '.woff2')) format('woff2'),
         url(font-url($file + '.woff')) format('woff'),
         url(font-url($file + '.ttf')) format('truetype'),
         url(font-url($file + '.svg#' + $family)) format('svg');
    font-weight: $weight;
    font-style: normal;
    font-display: swap;
  }
  @if $hack-chrome-windows {
    @media screen and (-webkit-min-device-pixel-ratio: 0) {
@font-face {
        font-family: $family;
        src: url(font-url($file + '.svg#' + $family)) format('svg');
        font-display: swap;
      }
    }
  }
}

@include webfont('Druk Wide Cy Web Bold Regular', 'Druk Wide Cy Web Bold Regular/Druk Wide Cy Web Bold Regular', $weight: normal);
@include webfont('Montserrat', 'Montserrat/poppins-extrabold', $weight: 800);
@include webfont('Montserrat', 'Montserrat/Montserrat-Medium', $weight: 500);
@include webfont('Montserrat', 'Montserrat/Montserrat-Regular', $weight: 400);
