@import "include/bs-grid/grid";
@import 'utils/vars';
@import 'utils/mixins';
@import 'utils/fonts';

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

a {
  color: inherit;
}.logo img {
  display: block;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
  max-width: 100%;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button {
  outline: none;
}[type='submit']  {
  cursor: pointer;
}
button {
  border: none;
}
*, *:before, *:after {
  box-sizing: border-box;
}

@media (min-width: 1101px) {
  a, [type=submit]  {

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
}



/*site styles*/
html {
  font-size: $baseFontSize;
  min-height: 100%;
  line-height: 1.2;
  overflow-x: hidden;

}

body {
  color: #000;
  font-family: $ff-text;
  overflow-x: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
  &.hidden {
    overflow: hidden;
  }
}.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: calc(100vh);
}.container {
  // width: $cw;
  width: 87.9%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @media (max-width: 1024px) {
    width: 100%;
  }
}
h2 {
  font-family: $ff;
  font-size: 100px;
  line-height: 1;
  text-transform: uppercase;
  @media (max-width: 767px) {
    font-size: 40px;
  }
}
h3 {
  font-size: 30px;
  line-height: 36px;
  font-weight: 800;
  font-family: $ff;
}
p {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  font-family: $ff-text;
  @media (max-width: 600px) {
    font-size: 18px;
  }
}
.title {
  font-family:$ff;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 800;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  font-size: 0;
  line-height: 0;
  letter-spacing: 0;
  width: 100px;
  height: 100px;
  transition: all .5s ease-in;

  .contact-text {
    position: absolute;
    animation: 6s linear infinite rotateCircle;
    fill: #fff;
  }
  .arrow-svg {
    fill: #000;
    stroke: #fff;
    max-width: 35px;
    transition: all .3s ease;
  }
  &:hover {
    .arrow-svg {
      transition: all .3s ease;
      fill: #fff;
      stroke: #000;
      transform: translateX(3px);
    }
  }
  &_blue {
    .contact-text {
      fill:#0000FF;
    }
    .arrow-svg {
      fill: #fff;
      stroke: #0000FF;
      transition: all .3s ease;
    }
    &:hover {
      .arrow-svg {
        transition: all .3s ease;
        fill: #0000FF;
        stroke: #fff;
      }
    }
  }
}

.link-back {
  font-size: 16px;
  font-family: $ff;
  text-decoration: none;
  text-transform: uppercase;
  color: #808080;
  display: flex;
  align-items: center;
  transition: color .3s ease;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  @media (max-width: 768px) {
    font-size: 14px;
  }
  svg {
    fill: #808080;
    margin-right: 24px;
    transition: all .3s ease;
    @media (max-width: 768px) {
      margin-right: 14px;
    }
  }
  &:hover {
    color: #0000FF;
    svg {
      transform: translateX(-3px);
      fill: #0000FF;
    }
  }
}
.more {
  display: flex;
  align-items: center;
  text-decoration: none;
  text-transform: uppercase;
  font-family:$ff;
  font-weight: 800;
  span {
    padding-right: 24px;
  }
   .arrow-svg {
      max-width: 32px;
      fill: #fff;
      stroke: #000;
      transition: all .3s ease;
   }
   &:hover {
     .arrow-svg {
      transform: translateX(3px);
      fill: #000;
      stroke: #fff;
      // transform: translateX(4px);
      }
   }
  &_blue {
    color: #00F;
    .arrow-svg {
      fill:#00F;
      stroke: transparent;
    }
    &:hover {
      .arrow-svg {
        fill: transparent;
        stroke: #00F;
      }
    }
  }
}

.label {
  font-size: 20px;
  font-family: $ff-text;
  text-transform: uppercase;
  display: block;
  transition: 0.6s;
  color: #FFF;
  a, span {
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    transition: color .3s ease;
    &:first-child{
      padding-right: 6px;
      position: relative;
      &:after {
        content: "";
        display: block;
        height: 20px;
        width: 2px;
        background-color: #FFF;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &:last-child{
      padding-left: 5px;
      &:after {
        content: none;
      }
    }
  }
  a{
    &:hover {
      color: $hover;
    }
  }
}
.reverse {
  flex-direction: row-reverse;
}

.cookie_bar {
  text-align: left;
  position: fixed;
  z-index: 997;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 1rem 0;
  background: #fff;
  border: 4px solid #000;
  transition: .3s ease;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  color: #000;
    &.active {
      transform: translateY(0);
    }
    .page_content {
      width: 1400px;
      max-width: 100%;
      display: block;
      margin: 0 auto;
      padding: 0 15px;
      @media (max-width: 1440px){
        width: 1200px;
      }
      @media (max-width: 1280px){
        width: 1030px;
      }
    }
    .cookie_text {
      line-height: 1.56;
      font-size: 12px;
      a {
        position: relative;
        color: inherit;
        white-space: nowrap;
        text-decoration: none;
        margin-left: 5px;
        font-family: $ff;
        text-transform: uppercase;
        transition: color .3s ease;
        &:hover {
          color: $hover;
        }
      &:after {
        display: block;
        content: '';
        position: absolute;
        top: 100%;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        height: 0;
        border-bottom: 1px solid #000;
        width: 50%;
        transition: .2s ease;
        opacity: 0;
      }
    }
  }
  .cookie_close_btn {
    text-decoration: none;
    font-family: $ff;
    text-transform: uppercase;
    margin-top: 8px;
    display: inline-block;
    color: inherit;
    font-size: 12px;
    svg {
      width: 16px;
      margin-left: 8px;
      margin-right: 0;
      transition: .2s ease;
      }
    span, svg {
      display: inline-block;
      vertical-align: middle;
    }
    span {
      line-height: 1.8;
    }
    &:hover {
      color: inherit;
      svg {
        margin-left: 5px;
        margin-right: 3px;
      }
    }
  }
}



.popup {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  display: none;
  &.active {
    display: flex;
  }
  &_thanks {
    .popup__wrap {
      max-width: 940px;
      padding: 135px 180px;
      @media (max-width: 1024px) {
        max-width: 700px;
        padding: 80px;
      }
      @media (max-width: 767px) {
        padding: 60px 40px;
      }

    }
  }
  &_privacy-policy {
    .popup__wrap {
      text-align: left;
      max-width: 1170px;
      padding: 60px 40px 40px;
      @media (max-width: 767px) {
        padding: 40px 20px;
      }
    }
    .popup__title{
      text-transform: uppercase;
      font-size: 16px;
      font-family: $ff;
      margin-bottom: 40px;
    }
    p {
      margin-bottom: 20px;
    }
  }
  &__wrap {
    background-color: #FFF;
    border: 10px solid #000;
    text-align: center;
    position: relative;

  }
  &__title {
    margin-bottom: 60px;
  }
  &__text {
    font-weight: 500;
  }
  &__close {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    @media (max-width: 767px) {
      top: 15px;
      right: 15px;
      width: 30px;
      height: 30px;
    }
    &:before, &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 46%;
      width: 100%;
      height: 5px;
      background: #0000FF;
      transition: 0.3s;
    }
    &:before {
      transform: rotate(45deg);
    }&:after {
      transform: rotate(-45deg);
    }
  }
  &__ov-h {
    overflow-y: scroll;
    max-height: 405px;
    padding-right: 10px;
    @media (max-width: 767px) {
      max-height: 300px;
    }
    &::-webkit-scrollbar-track {
      background-color: #E8E8E8;
    }
    &::-webkit-scrollbar {
      width: 10px;
      background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $hover;
      width: 10px;
      height: 2px;
    }
  }
}

@keyframes rotateCircle {
  0% {
   transform: rotate(0);
  }
  100% {
   transform: rotate(360deg);
  }
}

// Page 4040
.page404 {
  .contact-us, .footer {
    display: none;
  }
}

.page-404 {
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  .contact-us {
    display: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-size: 56px;
    line-height: 80px;
    font-weight: bold;
    font-family: $ff;
    margin-bottom: 20px;
    @media (max-width: 1080px) {
      font-size: 40px;
    }
  }
  &__desc {
    font-family: $ff;
    margin-bottom: 60px;
  }

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url(../img/svg/bg404.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;
    @media (max-width: 1080px) {
      background-position:  right bottom;

    }
  }
  .more {
    svg {
      fill: #00F;
      margin-right: 20px;
      transition: transform .3s ease;
    }
    &:hover {
      svg {
        transform: translateX(-3px);
        transition: transform .3s ease;
      }
    }
  }
}

ul.lang {
  position: relative;
  cursor: pointer;
  &:hover {
    &:after {
      // opacity: 1;
      // display: block;
      width: 40px;
    }
    a {
      opacity: 1;
      pointer-events: auto;
      width: 100%;
    }

  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 40px;
    height: 2px;
    background-color: #808080;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    transition: width .3s ease;
    width: 0;
  }
  li{
    display: inline-block;
    margin-bottom: 0 !important;
  }
  a {
    color: #808080;
    font-family: $ff;
    margin-right: 80px;
    display: inline-block;
    cursor: pointer;
    pointer-events: none;
    transition: opacity .3s ease;
    text-decoration: none;
    opacity: 0;
  }
  span {
    display: inline-block;
    font-family: $ff;
    color: #0000FF;
    font-size: 12px;
    text-transform: uppercase;
  }
  &_mob {
    display: none;
  }
  @media(max-width: 1024px){
    display: none;
    &_mob {
      display: inline-block !important;
      margin-bottom: 20px;
      a {
        opacity: 1;
        pointer-events: auto;
      }
      &:after {
        width: 40px;
        pointer-events: auto;
      }
    }
  }
}

