.contact-page {
	.contact-us {
		display: none;
	}
	.footer {
		display: none;
	}

	.contact {
		padding-top: 130px;
	    @media (max-width: 768px) {
	    	padding-top: 100px;
		}
		&__title {
			margin-bottom: 80px;
	    	@media (max-width: 768px) {
	    		margin-bottom: 40px;
			}
		}
	}
}
.contact {
	padding-top: 130px;
    @media (max-width: 1300px) {
    	padding-top: 100px;
	}
	&__title {
		text-align: center;
		margin-bottom: 80px;
    	@media (max-width: 1300px) {
    		margin-bottom: 40px;
		}
	}
	&_contact-form {
		transform: translateY(0);
		position: static;
		padding-top: 0;
    	@media (max-width: 1300px) {
			padding: 0;
		}
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		@media (max-width: 767px) {
			display: block;
		}
	}
	&__col {
		&:first-child {
			width: 60%;
			@media (max-width: 767px) {
    			width: 100%;
			}
		}
		&:last-child {
			width: 25%;
    		@media (max-width: 1300px) {
    			width: 35%;
			}
			@media (max-width: 767px) {
    			width: 100%;
			}
		}
	}
	.contact-form  {
		&__info {
			margin-bottom: 60px;
		}
		.form-item.checkox {
			margin-top: 40px;
		}
	}
	&__item {
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0;
		}
		.title {
			margin-bottom: 20px;
		}
		a, .adr {
			font-size: 20px;
			text-decoration: none;
			font-weight: 500;
		}
	}
}
