.hero {
	height: calc(100vh - 115px);
	background-color: #000;
	color: #fff;
	position: relative;
	z-index: 12;
	// padding-top: 115px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: default;
    @media (max-width: 1280px) {
		height: calc(100vh - 95px);
	}
    @media (max-width: 767px) {
    	height: auto;
	}
	 @media (max-width: 1024px) and (orientation: landscape){
    	height: auto;
	}

	&__top {
		padding-top: 150px;
    	@media (max-width: 768px) {
    		padding-top: 30px;
		}
	}
	h1 {
		font-size: 56px;
		font-weight: 800;
		font-family: $ff;
		color: #FFF;
		text-transform: capitalize;
		position: relative;
    	@media (max-width: 768px) {
    		font-size: 35px;
		}
    	@media (max-width: 400px) {
    		font-size: 32px;
		}
	}
	&__desc {
		color: #FFF;
		position: relative;
		padding-top: 135px;
		padding-bottom: 30px;
    	@media (max-width: 1024px) {
    		padding: 30px 0;
		}
	}
	.container {
		position: relative;
	}
	&__main-text {
		position: absolute;
		right: 0;
		width: 770px;
		height: 465px;
    	@media (max-width: 1440px) {
    		top: 50px;
		}
    	@media (max-width: 1024px) {
    		display: none;
		}
	}
	&__desc {
		font-family: $ff-text;
		font-size: 20px;
		font-weight: 500;
		max-width: 310px;
	}
	&__img-mob {
		display: none;
    	@media (max-width: 1024px) {
			display: block;
			svg {
				width: 100%;
			}
		}
	}
}
.scrollable-top {
	padding-top: 115px;
    @media (max-width: 1280px) {
		padding-top: 95px;
	}
	@media (max-width: 480px) {
		padding-top: 80px;
	}
}
