			display: block;
			padding-bottom: 40px;
			.more {
				margin-top: 30px;
			}
		}
	}
}

.why-we {
	overflow: hidden;
	position: relative;
	z-index: 9;
	padding-top: 502px;
	@media (max-width: 1024px) {
		padding-top: 0;
	}
	&__anim {
		padding: 60px 0 140px;
		background-color: #FFF;
		@media (max-width: 1024px) {
			padding: 60px 0;
		}
		.text-top, .text-bottom {
			font-weight: 800;
			font-family: $ff;
			 -webkit-text-stroke: 3px #0000FF;
			-webkit-text-fill-color: white;
			font-size: 76px;
			white-space: nowrap;
			text-transform: uppercase;
			display: block;
			@media (max-width: 1024px) {
				font-size: 45px;
			}
		}
		span {
			display: inline-block;
		}
	}
	&__fixed {
		position: fixed;
		width: 100%;
		height: 502px;
		left: 0;
		bottom: 0;
		background-color: #FFF;
		z-index: 99;
		opacity: 0;
		&.show {
			opacity: 1;
		}
		&.static {
			position: absolute;
			left: 0;
			top: 0;
			@media (max-width: 1024px) {
				position: static;
			}
		}
		@media (max-width: 1024px) {
			position: static;
			opacity: 1;
			height: auto;
		}
	}
	.text-top {
		transform: rotate(-3deg) translateX(-225.6%);
		animation: 40s linear infinite moveTextRight;
	}
	.text-bottom {
		transform: rotate(4deg) translateX(-58%);
		animation: 40s linear infinite moveTextLeft;
    	padding-top: 120px;
	}

	&__text {
		color: #000;
		margin-bottom: 150px;
		@media (max-width: 1024px) {
			margin-bottom: 80px;
		}
	}

	&__title {
		font-size: 30px;
		font-weight: 800;
		font-family: $ff;
		margin-bottom: 48px;
		line-height: 36px;
	}
	&__lists {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		@media (max-width: 1024px) {
			display: block;
		}
		ul {
			margin-right: 170px;
			@media (max-width: 1024px) {
				margin-right: 0;
			}
			li {
				margin-bottom: 24px;
				position: relative;
				padding-left: 23px;
				&:before {
					content: "";
					display: block;
					position: absolute;
					height: 4px;
					width: 15px;
					left: 0;
					top: 10px;
					background-color: #000;
				}
				p {
					font-size: 20px;
					line-height: 24px;
					font-weight: 500;
				}
			}
		}
	}
}

@keyframes moveTextRight {
  100% {
   transform: rotate(-3deg) translateX(-79%);
  }
}
@keyframes moveTextLeft {
  100% {
   transform: rotate(4deg) translateX(-356.2%);
  }
}


.about {
	margin-top: 250px;
	padding-bottom: 200px;
	position: relative;
	z-index: 12;
	background-color: #FFF;
	@media (max-width: 1024px) {
		margin-top: 100px;
		padding-bottom: 100px;
	}
	@media (max-width: 767px) {
		margin-top: 8	0px;
		padding-bottom: 60px;
	}
	&__title {
		font-family: $ff;
		font-size: 30px;
		max-width: 585px;
		@media (max-width: 1024px) {
			margin-bottom: 50px;
		}
	}
	&__top	{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 56px;
		@media (max-width: 1024px) {
			display: block;
			margin-bottom: 0;
		}
	}

	&__anim {
		position: relative;
		@media (max-width: 480px) {
			display: none;
		}
		.line-2 {
			position: absolute;
			bottom: 0;
			left: -82px;
		}
		svg {
			width: 100%;
			position: relative;
			@media (max-width: 1024px) {
				height: 282px !important;
			}
			@media (max-width: 600px) {
				height: 182px !important;
			}
		}
		.line {
			stroke-dasharray: 5;
			stroke-dasharray: 1000;
			stroke-dashoffset: 1000;
			stroke: #0000FF;
			// animation: 7s dash 0s linear alternate infinite;
			&_1 {
				&_1 {
					animation: 5s dash 0s linear alternate infinite;
				}
				&_2 {
					animation: 5s dash 2s linear alternate infinite;
				}
				&_3 {
					animation: 5s dash 3s linear alternate infinite;
				}
				&_4 {
					animation: 5s dash 6s linear alternate infinite;
				}
			}
			&_2 {
				&_1 {
					animation: 5s dash 0s linear alternate infinite;
				}
				&_2 {
					animation: 5s dash 2s linear alternate infinite;
				}
				&_3 {
					animation: 5s dash 3s linear alternate infinite;
				}
				&_4 {
					animation: 5s dash 6s linear alternate infinite;
				}
			}
			&_3 {
				&_1 {
					animation: 5s dash 0s linear alternate infinite;
				}
				&_2 {
					animation: 5s dash 2s linear alternate infinite;
				}
				&_3 {
					animation: 5s dash 4s linear alternate infinite;
				}
				&_4 {
					animation: 5s dash 6s linear alternate infinite;
				}
			}
		}
	}
	&__anim-mob {
		display: none;
		@media (max-width: 480px) {
			display: block;
			margin-top: 20px;
			svg {
				width: 100%;
			}
		}
	}
}

@keyframes dash {
  to{
    stroke-dashoffset: 0;
  }
}


.solutions {
	&__inner {
		position: relative;
	}
	.container {
		position: relative;
	}
	&__title {
		text-transform: uppercase;
		font-family: $ff;
		font-weight: 800;
		font-size: 16px;
		line-height: 19px;
		position: absolute;
		left: 0;
		top: 100px;
		@media (max-width: 1024px) {
			position: static;
			padding-bottom: 30px;
		}
	}
	&__list {
		position: relative;
		z-index: 9;
		transition: color .3s ease;
		&:hover {
			.solutions__link {
				color: rgba(0, 0, 0, 0.5);
			}
		}
	}
	&__link {
		text-decoration: none;
		text-transform: uppercase;
		display: block;
		text-align: center;
		font-family: $ff;
		font-size: 30px;
		line-height: 36px;
		font-weight: 800;
		transition: color .3s ease;
		padding: 12px 0;
		@media (max-width: 767px) {
			font-size: 25px;
		}
	}

	&__item {
		&.active {
			.solutions__link {
				color: #000;
			}
		}
	}
	&__fixed-img {
		min-width: 740px;
		height: 464px;
		pointer-events: none;
		top: 50%;
		left: 50%;
		-webkit-transform: translateY(-50%) translateX(-50%);
		-moz-transform: translateY(-50%) translateX(-50%);
		-ms-transform: translateY(-50%) translateX(-50%);
		-o-transform: translateY(-50%) translateX(-50%);
		transform: translateY(-50%) translateX(-50%);
		overflow: hidden;
		transition: opacity .5s ease;
		opacity: 0;
		position: absolute;
		div {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			transform: scale(1.1);
			background-position: top center;
			background-size: cover;
			background-repeat: no-repeat;
			transition: transform .5s ease;
		}
		&.fixed {
			position: fixed;
			opacity: 0;
			transition: opacity .3s ease;
		}
		&.active {
			opacity: 0.4;
			div {
				transform: scale(1);
			}
		}
	}
}


