.team {
	position: relative;
	height: 400vh;
	overflow: hidden;
	margin-top: -2px;
	cursor: default;
	@media (max-width: 1024px) {
		height: 100vh;
	}
	@media (max-width: 600px) {
		height: 80vh;
	}
	.container {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	&__anim-img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 50%;
		left: 50%;
		z-index: 97;
		transform: translate(-50%, -50%);
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		pointer-events: none;
		@media (max-width: 1024px) {
			display: none;
		}
	}
	&__static {
		// padding-top: 150px;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		height: 100vh;
		position: relative;
		width: 100%;
		display: flex;
		@media (max-width: 600px) {
			height: 80vh;
		}
		&.fixed {
			position: fixed;
			left: 0;
			top: 0;
			z-index: 96;
			height: 100%;
			transition: top .1s linear;
		}
		&.absolute {
			position: absolute;
			left: 0;
			bottom: 0;
			height: 100vh;
		}
	}
	&__title {
		font-size: 56px;
		line-height: 80px;
		font-weight: 800;
		font-family: $ff;
		text-transform: capitalize;
		margin-bottom: 40px;
		@media (max-width: 768px) {
    		font-size: 35px;
    		line-height: 50px;
		}
	}
	&__inner {
		opacity: 0;
		position: relative;
		// z-index: 97;
		transition: opacity .5s ease;
		&.show {
			transition: opacity .3s ease;
			opacity: 1;
			.team__title {
    			animation: up-anim 2s ease;

			}
			.more {
    			animation: up-anim 2s ease;

			}
		}
		@media (max-width: 1024px) {
			opacity: 1;
		}
	}
