.projects {
	padding-top: 155px;
	color: #FFF;
	@media (max-width: 767px) {
		padding-top: 120px;
	}
	&__bg-img {
		position: fixed;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		filter: grayscale(100%);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		opacity: 0.5;
		transition: opacity .3s ease;
		&:after {
			content:'';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: rgba(0,0,0,.5);
		}
		&.active {
			opacity: 1;
			transition: opacity .3s ease;
		}
	}
	&__wrap {
		padding: 60px 40px 0;
		position: relative;
		z-index: 9;
		@media (max-width: 767px) {
			padding: 40px 15px 0;
		}
	}
	.container {
		position: relative;
		z-index: 9;
	}
	&__title {
		font-size: 56px;
		line-height: 80px;
		font-family: $ff;
		font-weight: 800;
		@media (max-width: 768px) {
    		font-size: 35px;
    		line-height: 1;
		}
	}
	&__inner {
		display: flex;
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		margin: 0 -10px;
		@media (max-width: 767px) {
			margin: 0;
		}
	}
	&__item-outer {
		width: 50%;
		padding: 0 15px;
    	@media (max-width: 767px) {
    		width: 100%;
    		padding: 0;
    	}

	}
	.item {
		display: inline-block;
		text-decoration: none;
		width: 100%;
		margin-bottom: 40px;
		&:hover {
			.item__img {
				filter: grayscale(0%);
			}
		}
		&__title {
			display: inline-block;
			margin-bottom: 14px;
		}
		&__img {
			display: block;
			padding-bottom: 65%;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			filter: grayscale(100%);
			transition: 0.6s;
			margin-bottom: 30px;
			@media (max-width: 767px) {
				padding-bottom: 55%;

			}
		}
	}
}

.projects-page {
	background-color: #000;
	 .header__menu-btn {
	 	svg {
	 		fill:#fff ;
	 		}
	 }
	.header {
		background-color: rgba(0,0,0,0.8);
		color: #fff;
		.logo {
			svg {
				fill: #fff;
			}
		}
	}
	.footer {
		background-color: #000;
		color: #FFF;
		position: relative;
		z-index: 9;
	}
	.contact-us__title {
		color: #FFF;
	}
	.btn_blue .contact-text {
		fill:#fff;
	}
	.btn_blue .arrow-svg {
		 fill: transparent;
   		stroke: #fff;
	}
	.btn_blue:hover .arrow-svg {
	    fill: #fff;
	    stroke: #fff;
    }
    .contact-us {
    	z-index: 9;
    	background-color: #000;
    }
    &.show {
		@media (min-width: 1024px) {
	    	.projects__item-outer:first-child,
	    	.projects__item-outer:nth-child(2), .projects__item-outer:nth-child(3), .projects__item-outer:nth-child(4){
	    		animation: up-anim 2s ease;
    		}
   		}
    }
    &.openNav.cf-open {
	    .header {
	      background-color :#fff;
	      transition: background 0.3s ease 0s;
	      .logo svg {
	      	fill: #000;
	      }
   		}
	}
}
