.footer {
	height: 50%;
	padding: 60px 0;
	background: #fff;
	color: #000;
  	@media (max-width: 767px) {
  		padding: 30px 0 40px;
	}
	.container {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
  		@media (max-width: 767px) {
  			display: block;
  			text-align: center;
		}
	}
	.title {
		margin-bottom: 24px;
	}
	a, .adr {
		font-weight: 400;
		font-size: 20px;
		font-family: $ff-text;
	}
	a {
		text-decoration: none;
		transition: color .3s ease;
		&:hover {
			color: #0000FF;
		}
	}
	&__col {
		width: 26%;
		&:nth-child(2){
			width: 32%;
  			@media (max-width: 767px) {
  				width: 100%;
			}
		}
		&:nth-child(3){
			width: 25%;
  			@media (max-width: 767px) {
  				width: 100%;
  				margin-bottom: 0;
			}
		}
  		@media (max-width: 767px) {
  			width: 100%;
  			margin-bottom: 20px;
		}
	}
}
